@import '../../../assets/styles/Variables.scss';

.collection {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  width: 100%;
  padding-inline: 50px;
  box-sizing: border-box;

  @media screen and (max-width: 768px) {
    padding-inline: 30px;
  }

  &__title {
    align-self: flex-start;

    @media screen and (max-width: 768px) {
      text-align: center;
    }

    &:focus {
      outline: none;
    }

    &.focus-visible {   
      outline: 3px solid $main-color;
      outline-offset: 3px;
      border-radius: 5px;
    }
  }

  &__cards {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
    width: 90%;

    @media screen and (max-width: 768px) {
      width: 100%;
    }
  }

  &__txt {
    font-size: 20px;
    text-align: center;

    @media screen and (max-width: 768px) {
      font-size: 16px;
    }    
  }
}